/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

body,
button {
  font-weight: 700;
  font-family: Montserrat, sans-serif;
  line-height: normal;
}

.heading {
  color: #6d708d;
  font-size: 3.2rem;
  text-transform: capitalize;
}

.switch {
  position: relative;
  display: inline-block;
  width: 5.6rem;
  height: 3.2rem;
  color: #b3b5c6;
  font-size: 1.5rem;
  background: linear-gradient(to right, #a3a8f0, #696fdd);
  border-radius: 1.6rem;
  transform: translateX(2px);
  cursor: pointer;
  transition: 0.25s;
}
.switch_inner {
  position: absolute;
  top: 0.4rem;
  left: 0.4rem;
  width: 2.4rem;
  height: 2.4rem;
  background: white;
  border-radius: 50%;
  transition: 0.25s;
}
.switch:hover {
  color: #696fdd;
  filter: brightness(1.3);
}
.switch::before {
  position: absolute;
  top: 0;
  right: calc(100% + 2.4rem);
  transform: translateY(30%);
  content: "Annually";
}
.switch::after {
  position: absolute;
  top: 0;
  left: calc(100% + 2.4rem);
  transform: translateY(30%);
  content: "Monthly";
}

input[type=checkbox]:focus + .switch {
  box-shadow: 0 0 0 0.1rem #6d708d;
}
input[type=checkbox]:checked + .switch > .switch_inner {
  left: 2.8rem;
}

.offer {
  --c-primary: #494c5f;
  --c-secondary: #6d708d;
  padding: 3.4rem 2.9rem 3.1rem;
  color: var(--c-secondary);
  text-align: center;
  background: white;
  border-radius: 0.8rem;
  box-shadow: 0 4rem 2.4rem rgba(105, 111, 221, 0.1);
}
@media (min-width: 64rem) {
  .offer {
    padding-right: 3.1rem;
    padding-left: 3.1rem;
  }
}
.offer_heading {
  font-size: 1.8rem;
}
.offer_price {
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1.8rem -0.7rem 0;
  color: var(--c-primary);
  font-size: 3rem;
}
@media (min-width: 23.4375rem) {
  .offer_price {
    font-size: 4rem;
  }
}
@media (min-width: 48rem) {
  .offer_price {
    font-size: 3rem;
  }
}
@media (min-width: 64rem) {
  .offer_price {
    font-size: 4rem;
  }
}
.offer_price::before {
  content: "$";
}
.offer_price::after {
  margin-left: 0.4rem;
  font-size: 1.8em;
  letter-spacing: -0.03em;
  content: attr(data-monthly);
}
@media (min-width: 64rem) {
  .offer_price::after {
    margin-left: 0.7rem;
  }
}
.offer > .list {
  margin-top: 2.4rem;
}
.offer > .btn {
  margin-top: 3.2rem;
}
.offer-highlighted {
  --c-primary: white;
  --c-secondary: white;
  background: linear-gradient(to bottom, #a3a8f0, #696fdd);
}

input:not(:checked) ~ .plans .offer_price::after {
  content: attr(data-annually);
}

.list_item {
  padding: 1.7rem;
  color: inherit;
  font-size: 1.5rem;
  line-height: 125%;
  text-align: center;
  border-top: 0.1rem solid #dbdbdb;
}
.list_item:last-child {
  border-bottom: 0.1rem solid #dbdbdb;
}

.btn {
  position: relative;
  width: 100%;
  padding: 1.4rem;
  color: white;
  font-size: 1.3rem;
  letter-spacing: 0.11em;
  text-transform: uppercase;
  background: right/250% no-repeat linear-gradient(to right, white 0%, white 40%, #a3a8f0 60%, #696fdd 100%);
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: color 0.25s, box-shadow 0.25s, transform 0.15s, background-position 0.4s;
}
.btn::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: calc(100% - 1rem);
  height: 0.2rem;
  background: #696fdd;
  transform: translateX(0.5rem);
  opacity: 0;
  transition: 0.25s;
  content: "";
}
.btn:focus {
  outline: none;
}
.btn:focus::before {
  bottom: -0.4rem;
  opacity: 1;
}
.btn:hover {
  color: #696fdd;
  background-position: left;
  box-shadow: inset 0 0 0 0.1rem #6d708d;
}
.btn:active {
  transform: scale(0.97);
}
.btn-inverse {
  color: #696fdd;
  background-image: linear-gradient(to right, transparent 0%, transparent 40%, white 60%, white 100%);
}
.btn-inverse::before {
  background: white;
}
.btn-inverse:hover {
  color: white;
  box-shadow: inset 0 0 0 0.1rem white;
}

body {
  min-height: 100vh;
  padding: 6.4rem 2.4rem 7rem;
  background: top -3.6rem right -19.5rem no-repeat url(bg-top.svg), #f6f6fe;
}
@media (min-width: 48rem) {
  body {
    padding-top: 7.1rem;
    background: top right no-repeat url(bg-top.svg), bottom left no-repeat url(bg-bottom.svg), #f6f6fe;
  }
}

.content {
  display: flex;
  flex-flow: column;
  align-items: center;
}
.content > .switch {
  margin-top: 4rem;
}
.content > .plans {
  margin-top: 8rem;
}
@media (min-width: 48rem) {
  .content > .plans {
    margin-top: 6.5rem;
  }
}

.plans {
  display: grid;
  grid-gap: 3.2rem 0;
  width: 100%;
  max-width: 40rem;
}
@media (min-width: 48rem) {
  .plans {
    grid-auto-columns: 1fr;
    grid-auto-flow: column;
    align-items: center;
    max-width: 105rem;
  }
  .plans > .offer:nth-child(1) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .plans > .offer:nth-child(2) {
    padding-top: 5.7rem;
    padding-bottom: 5.4rem;
  }
  .plans > .offer:nth-child(3) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
}